body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #141285;
}

main {
  max-width: 1400px;
  background-color: #f0f0f0;
  border-radius: 5px;
  padding-top: 20px;
  padding-left: 40px;
  padding-right: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.search-form {
  width: 400px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex: 1;
  padding: 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #141285;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
}

button.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #141285;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.gallery {
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

